export const BookingTicketStyled = styled.div`
  position: relative;
  height: 100rem;
  margin-top: 20rem;
  & > * { transition: all 0.3s ease }
  :not(:last-child) { margin-right: 20rem }
  svg:first-of-type {
    cursor: pointer;
    height: 100%;
    color: var(--main-light);
  }
  span {
    pointer-events: none;
    font-size: 18rem;
    color: var(--white);
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
  }
  svg:last-of-type {
    pointer-events: none;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    height: 50rem;
    color: var(--error);
    z-index: 2;
  }
  :hover {
    svg:first-of-type { opacity: 0.5 }
    svg:last-of-type { opacity: 1 }
  }
`;

