import React, { memo, useMemo } from 'react';
// import { TBooking } from '../../api/model/Booking';
import { TResource } from '../../api/model/Resource';
import { TSlot } from '../../api/model/Slot';
import { DAYS, MONTHS } from '../../utility/constants';
import { calcPercentage, formatDate, formatTime } from '../../utility/functions';
// import { useGet } from '../../utility/hooks';
import { ChevronRight, Close, Ticket, Plus } from '../../utility/Icons';
import { Flex } from '../Lib';
import { CalendarDialogStyled, PercentageBar, Title, Bookings, BookingTicketStyled, CalendarDialogButton } from './dialogStyles';
// import { mockBookings } from './mocks';

type TSlotTimesString = {
  weekDay: string,
  day: string,
  month: string,
  hour: string,
}
// TODO: mettere listBookings
// const fetchBookings = async () => mockBookings as TBooking[];

const CalendarDialog = memo(({ resource, slot, ...rest }: { resource: TResource, slot: TSlot, isOpen: boolean, onDismiss: () => void }) => {
  // const { data: bookings } = useGet<TBooking[]>(fetchBookings);
  
  const slotTimes = useMemo(() => {
    const genDateObj = (date: Date) => ({
      weekDay: DAYS[date.getDay() - 1],
      day: formatDate(date).slice(8, 10),
      month: MONTHS[formatDate(date).slice(5, 7)],
      hour: formatTime(date)
    });
    const startDate = genDateObj(new Date(slot.starts_at.datetime));
    const endDate = genDateObj(new Date(slot.ends_at.datetime));
    const genDateString = ({ weekDay, day, month, hour}: TSlotTimesString) => `${weekDay} ${day} ${month} - ${hour}`;
    return (
      <Flex>
        {genDateString(startDate)}
        <ChevronRight />
        {genDateString(endDate)}
      </Flex>
    );
  }, [slot.starts_at.datetime, slot.ends_at.datetime]);

  // TODO: eliminare
  const twoDigitNumber = (n: number) => n < 10 ? `0${n}` : n;
  
  return (
    <CalendarDialogStyled {...rest} aria-label="Booking Detail">
      <Title>
        <strong>{resource.name}</strong>
        {slotTimes}
      </Title>
      <PercentageBar percentage={calcPercentage(slot.bookings, slot.capacity)}>
        {`${slot.bookings} prenotazioni su ${slot.capacity}`}
      </PercentageBar>
      <Bookings>
        { [...Array(slot.bookings)].map((b, i) => <BookingTicket key={`tickets_${i}`} code={`AA${twoDigitNumber(i)}`} />)}
        {/* { bookings && bookings?.map(b => <BookingTicket key={`tickets_${b.id}`} code={b.code} />) } */}
      </Bookings>
      <Flex>
        <CalendarDialogButton><Plus />Aggiungi prenotazione</CalendarDialogButton>
      </Flex>
      <Close onClick={rest.onDismiss} />
    </CalendarDialogStyled>
  );
});

const BookingTicket = ({ code }: { code: string }) => (
  <BookingTicketStyled>
    <span>{code}</span>
    <Ticket />
    <Close />
  </BookingTicketStyled>
);

export default CalendarDialog;