import { TSlot } from '../../api/model/Slot';

export const initialState = {
  slots: new Map(),
  isOpen: false,
  selectedSlot: undefined,
  selectedWeek: 0,
};

type WeekObj = { [k: string]: TSlot | undefined }

export type TSlots = Map<string, WeekObj>;

type Action = {
  type: 'loadSlots' | 'open' | 'close' | 'previousWeek' | 'nextWeek' | 'today',
  data?: number | TSlot | TSlots,
};

type State = {
  slots: TSlots,
  isOpen: boolean,
  selectedSlot?: TSlot,
  selectedWeek: number,
};

export const reducer = (state: State, action: Action) : State => {
  const { type, data } = action;
  const { slots, selectedWeek } = state;
  
  switch (type) {
    case 'loadSlots': return { ...state, slots: data as TSlots };
    case 'open': return { ...state, isOpen: true, selectedSlot: data as TSlot };
    case 'close': return { ...initialState, slots, selectedWeek };
    case 'previousWeek': return { ...state, selectedWeek: selectedWeek - 1 };
    case 'nextWeek': return { ...state, selectedWeek: selectedWeek + 1 };
    case 'today': return { ...state, selectedWeek: 0 };
    default: return state;
  }
}