import { createContext, useContext, Dispatch } from 'react';
import { TResource } from './api/model/Resource';

export const initialState = {
  loading: false,
  user: null,
  resources: [],
  selectedResource: 0,
};

type Action = {
  type: 'toggleLoading' | 'setUser' | 'loadResources' | 'selectResource',
  data?: any,
};

type State = {
  loading: boolean,
  user: any, // TODO: inserire tipo user qui e in Action>data
  resources: TResource[],
  selectedResource: number,
};

type Context = {
  state: State,
  dispatch: Dispatch<any>
}

const GlobalContext = createContext<Context>({ state: initialState, dispatch: () => null });
export const Provider = GlobalContext.Provider;

export const reducer = (state: State, action: Action) : State => {
  const { data } = action;
  switch(action.type) {
    case 'toggleLoading': return { ...state, loading: !state.loading };
    case 'setUser': return { ...state, user: data };
    case 'loadResources': return { ...state, resources: data as TResource[] };
    case 'selectResource': return { ...state, selectedResource: data as number };
    default: return state;
  }
}

export const useGlobalContext = (): [State, Dispatch<any>] => {
  const { state, dispatch } = useContext(GlobalContext);
  return [state, dispatch];
}