import React, { memo, ReactNode } from 'react';
import { HeaderStyled, Logo, MenuListStyled, ResourceButton, MenuWrapper, ResourceMenuList, UserMenuLink } from './styles';
import { Menu, MenuButton, MenuItem, MenuLink } from "@reach/menu-button";
import { auth } from '../../firebase';
import { Cog, ChevronDown, ChartPie, Logout } from '../../utility/Icons';
import { TResource } from '../../api/model/Resource';
import { Link } from 'react-router-dom';

const Header = memo((
  { resources, selectedResource, handleResourceSelect, children }:
  { resources?: TResource[], selectedResource?: number, handleResourceSelect?: (data: number) => void, children?: ReactNode }
) => (
  <HeaderStyled>
    <Logo>
      <Link to="/">BiBooking</Link>
      { resources && resources.length > 0 && selectedResource !== undefined && (
        <Menu>
          <MenuButton>
            <ResourceButton>
              <span>{resources && resources[selectedResource]?.name}</span>
              <ChevronDown />
            </ResourceButton>
          </MenuButton>
          <ResourceMenuList>
            { resources?.map((r, i) => <MenuItem key={`menu_${i}_${r.id}`} onSelect={() => handleResourceSelect?.(i)}>{r.name}</MenuItem>) }
          </ResourceMenuList>
        </Menu>
      )}
    </Logo>
    {children}
    <MenuWrapper>
      <Menu>
        <MenuButton>
          <Cog />
        </MenuButton>
        <MenuListStyled>
          {/* <MenuLink as={Link} to="/users">
            <UserMenuLink><Users />Utenti</UserMenuLink>
          </MenuLink> */}
          <MenuLink as={Link} to="/stats">
            <UserMenuLink><ChartPie />Statistiche</UserMenuLink>
          </MenuLink>
          <MenuItem onSelect={() => auth.signOut()}>
            <UserMenuLink><Logout />Esci</UserMenuLink>
          </MenuItem>
        </MenuListStyled>
      </Menu>
    </MenuWrapper>
  </HeaderStyled>
));

export default Header;