import React, { useEffect, useReducer, useMemo, useCallback } from 'react';
import { Switch, Route } from 'react-router-dom';
import styled from 'styled-components';
import Calendar from './components/Calendar';
import { mockResources } from './components/Calendar/mocks';
import { Flex } from './components/Lib';
import Login from './components/Login';
import Stats from './components/Stats';
import Users from './components/Users';
import { reducer, initialState, Provider } from './context';
import { auth, generateUserDocument } from './firebase';
import { Loading } from './utility/Icons';

function App() {
  const [state, dispatch] = useReducer(reducer, initialState);
  const { user, loading } = state;
  const contextValue = useMemo(() => ({ state, dispatch }), [state, dispatch]);

  useEffect(() => {
    auth.onAuthStateChanged(async user => {
      const data = await generateUserDocument(user);
      dispatch({ type: 'setUser', data });
    });
  }, []);

  const fetchResources = useCallback(async () => {
    try {
      // const resources = await listResources();
      const resources = mockResources; // TODO: chiamare resources;
      if (resources instanceof Error) return;

      dispatch({ type: 'loadResources', data: resources });
    } catch (error) {
      console.log(error);
    }
  }, []);

  useEffect(() => { fetchResources() }, [fetchResources]);

  return !user
    ? <NonLoggedRouter />
    : (
      <Provider value={contextValue}>
        { loading && <Overlay /> }
        <LoggedRouter />      
      </Provider>
    );
}

const Overlay = styled((props: any) => <Flex {...props}><Loading /></Flex>)`
  position: absolute;
  top: 0;
  height: 100vh;
  width: 100vw;
  background-color: var(--black);
  opacity: 0.8;
  z-index: 100;
  color: var(--white);
  svg {
    height: 60rem;
    @keyframes spinner {
      0% { transform: rotate(0deg) }
      to { transform: rotate(359deg) }
    }
    animation: spinner 1.2s cubic-bezier(.6,0,.4,1) infinite;
  }
`;

const LoggedRouter = () => (
  <Switch>
    <Route path="/users"><Users /></Route>
    <Route path="/stats"><Stats /></Route>
    <Route path="/"><Calendar /></Route>
  </Switch>
);

const NonLoggedRouter = () => (
  <Switch>
    <Route path="/"><Login /></Route>
  </Switch>
);

export default App;
