import React, { useCallback } from 'react';
import { useGlobalContext } from '../../context';
import Header from '../Header';
import andamento from '../../media/andamento.jpg';
import occupazione from '../../media/occupazione.jpg';
import { Flex } from '../Lib';

export default function Stats () {
  const [{ resources, selectedResource }, globalDispatch] = useGlobalContext();
  const handleResourceSelect = useCallback((data: number) => globalDispatch({ type: 'selectResource', data }), [globalDispatch]);

  return (
    <>
      <Header {...{ resources, selectedResource, handleResourceSelect }} />
      <Flex style={{ backgroundColor: 'white', height: 'calc(100vh - var(--header-height))' }} justifyContent="space-around">
        <img src={andamento} alt="Andamento settimanale" style={{ width: '45vw' }} />
        <img src={occupazione} alt="Occupazione settimanale" style={{ width: '45vw' }} />
      </Flex>
    </>
  );
}